
body {
  font-family: sans-serif;
  margin: 0;
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
}

.screen {
  background: rgb(135, 133, 255);
  overflow: hidden;
  display: flex;
  align-items: center;
  position: relative;
  justify-content: center;
}

.letters {
  position: absolute;
  color: white;
  font-size: 70px;
}